.container{background:linear-gradient(135deg, rgba(244,245,249,0.3), rgba(244,245,249,0.7) 250%);box-shadow:0 5px 20px rgba(0,0,0,0.3);padding:1.4rem;border-radius:.8rem;display:flex;flex-direction:column;align-items:center;justify-content:center}.container svg{width:10rem;height:10rem;filter:drop-shadow(0 5px 10px rgba(0,0,0,0.2))}.container svg>* path{fill:#f4f5f9}.container .header{font-size:2.375rem;color:#f4f5f9;text-shadow:1px 2px 10px rgba(0,0,0,0.2)}.container .description{font-size:.81rem;color:#144641;text-shadow:1px 1px 1px rgba(255,255,255,0.1)}.container .about{font-size:.94rem}
